import React from 'react';
import style from './style.module.css';
import { makeStyles } from '@material-ui/core/styles';
import FilledInput from '@material-ui/core/FilledInput';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import InputLabel from '@material-ui/core/InputLabel';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Defile from './Defile.png';
import NoDefile from './NoDefile.png';


const buttonStyle = {
  marginTop: "20px",
  width: "250px",
  heigth: '100px',
  paddingLeft: "-20px" 
};

const secondButtonStyle = {
  marginTop: "20px",
  width: "250px",
  heigth: '100px'
};


const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
}));

const SecondPage = (props) => {




  const [name, setName] = React.useState('Composed TextField');
  const classes = useStyles();

  const handleChange = (event) => {
    setName(event.target.value);
  };

  return (
    <div className = {style.superWrapper} id = 'secondPage'>
      <div className = {style.wrapper}>
        <div className = {style.firstOption}>
          <h1 className = {style.optionIcon}>🧚🏾‍♀️</h1>
          <div>
            <Button size="large" className={classes.margin} variant="outlined" style = {buttonStyle} onClick={(e) => {props.setState(false)}}>
            <h3 className = {style.buttonText}> <a href = '#thirdPage' style = {{textDecoration: "none"}} >Без дефиле</a></h3>
            </Button>
          </div>
        </div>
        <div className = {style.secondOption}>
          <h1 className = {style.optionIcon}>🤹🏾‍♀️</h1>
          <div>
            <Button size="large" className={classes.margin} variant="outlined" style = {buttonStyle} onClick={(e) => {
              props.setState(true);
              }}>
              <h3 className = {style.buttonText}> <a href = '#thirdPage' style = {{textDecoration: "none"}}>С дефиле</a></h3>
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SecondPage;