import React from 'react';
import style from './style.module.css';
import MainPhoto from './MainPhoto.jpg';
import NavBar from '../NavBar/NavBar';
import Gallery1 from './Gallery1.jpg';
import Gallery2 from './Gallery2.jpg';
import Gallery3 from './Gallery3.jpg';
import Gallery4 from './Gallery4.jpg';
import Gallery5 from './Gallery5.jpg';
import Gallery6 from './Gallery6.jpg';
import Gallery7 from './Gallery7.jpg';

const ProfilePage = () =>{
    return(
        <div className = {style.clearDiv}>
            <div className = {style.wrapper}>
                <NavBar/>
                <div className = {style.photoContainer}>
                    <img src = {MainPhoto} className = {style.mainPhoto} alt = 'MainPhoto'/>
                </div>
                <div className = {style.textContainer}>
                    <h2 className = {style.profileName}>@_it_ankaline</h2>
                    <h3 className = {style.profileMainText}>Lorem Ipsum - это текст-"рыба", часто используемый в печати и вэб-дизайне.
                        Lorem Ipsum является стандартной "рыбой" для текстов на латинице с начала XVI века. 
                        В то время некий безымянный печатник создал большую коллекцию размеров и форм шрифтов, 
                        используя Lorem Ipsum для распечатки образцов. Lorem Ipsum не только успешно пережил без заметных 
                        изменений пять веков, но и перешагнул в электронный дизайн. 
                    </h3>
                </div>
                <div className = {style.photoGrid}>
                    <div className = {style.Cell1}>
                        <h2 className = {style.gridTitle}>Галлерея</h2>
                    </div>
                    <div className = {style.Cell2}>
                        <img className = {style.cellImage} src = {Gallery1} alt = {'galleryImg'}/>
                    </div>
                    <div className = {style.Cell3}>
                        
                    </div>
                    <div className = {style.Cell4}>
                        <img className = {style.cellImage} src = {Gallery2} alt = {'galleryImg'}/>
                    </div>
                    <div className = {style.Cell5}>
                        <img className = {style.cellImage} src = {Gallery3} alt = {'galleryImg'}/>
                    </div>
                    <div className = {style.Cell6}>
                        
                    </div>
                    <div className = {style.Cell7}>
                        <img className = {style.cellImage} src = {Gallery4} alt = {'galleryImg'}/>
                    </div>
                    <div className = {style.Cell8}>
                    <img className = {style.cellImage} src = {Gallery5} alt = {'galleryImg'}/>
                    </div>
                    <div className = {style.Cell9}>
                        
                    </div>
                    <div className = {style.Cell10}>
                        <img className = {style.cellImage} src = {Gallery6} alt = {'galleryImg'}/>
                    </div>
                    <div className = {style.Cell11}>
                        <img className = {style.cellImage} src = {Gallery7} alt = {'galleryImg'}/>
                    </div>
                    <div className = {style.Cell12}>
                        
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProfilePage;