import React, { useState }  from 'react';
import style from './style.module.css'
import FirstPage from '../FirstPage/FirstPage';
import SecondPage from '../SecondPage/SecondPage';
import ThirdPage from '../ThirdPage/ThirdPage';
import FinalPage from '../FinalPage/FinalPage';

const ApplicationForm = () =>{


    const [state, setState] = useState(false);
     const [formState, setFormState] = useState(
        {Defile: {
            name: "",
            forename: "",
            nickname: "",
            noNickname: "",
            email: "",
            about: "",
            driveLink: "",
            defileDescription: ""
        },
        noDefile: {
            name: "",
            forename: "",
            nickname: "",
            noNickname: "",
            email: "",
            about: "",
            driveLink: "",
            socialLink: ""
        }

   })

    return(
        <div className = {style.wrapper}>
            <div className = {style.firstPage}>
                <FirstPage/>
            </div>
            <div className = {style.secondPage} >
                <SecondPage setState = {setState} setFormState = {setFormState} formState = {formState}/>
            </div>
            <div className = {style.thirdPage}>
                <ThirdPage state = {state} setFormState = {setFormState} formState = {formState}/>
            </div>
            <div className = {style.finalPage}>
                <FinalPage state = {state} setFormState = {setFormState} formState = {formState}/>
            </div>
        </div>
    )
}


export default ApplicationForm;