import React from 'react';
import style from './style.module.css';
import infoPhoto from './infoPhoto.jpg';

const MainText = () =>{
    return(
        <div className = {style.wrapper}>
                    <div className = {style.infoPhotoDiv}>
                        <img src = {infoPhoto} className = {style.infoPhoto}/>
                    </div>
                    <div className = {style.mainText}>
                        <h2>Гик-мероприятие без границ</h2>
                        <p>Для любителей фильмов, сериалов, комиксов, аниме, книг, настольных игр, манги, К-поп и косплея.
                            Мы начали поводить маленькие встречи подписчиков в 2015 году. Чуть позже, появилась идея - создать своё КРУПНОЕ мероприятие. В 2018 году, на первое FFP пришло 700 человек. Месяц спустя - их было уже 1700.
                        </p>
                    </div>
                    <div>
                        <a href = '/application' style = {{textDecoration: "none"}}>Пипипупу</a>
                    </div>

        </div>
    )
}

export default MainText;