import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import style from './style.module.css';
import Button from '@material-ui/core/Button';
import DoneIcon from '@material-ui/icons/Done';
import { makeStyles } from '@material-ui/core/styles';
import FilledInput from '@material-ui/core/FilledInput';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import TextField from '@material-ui/core/TextField';
import { green } from '@material-ui/core/colors';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';



const GreenCheckbox = withStyles({
  root: {
    color: green[400],
    '&$checked': {
      color: green[600],
    },
  },
  checked: {},
})((props) => <Checkbox color="default" {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
}));

const FirstPage = (props) => {
  const [name, setName] = React.useState('Composed TextField');
  const classes = useStyles();

  const handleChange = (event) => {
    setName(event.target.value);
  };

  const textBoxStyle = {
    width: "440px",
    heigth: '350px'
  };


  const submitButtonStyle ={
     width: "440px",
     backgroundColor: "#BAC94A"
  }


  return (
    <div className = {style.wrapper}>
      <div className = {style.leftSide}>
        <form className={classes.root} noValidate autoComplete="off">
          <div className = {style.firstRow}>
          <FormControl variant="outlined" >
              <InputLabel htmlFor="component-outlined">Имя</InputLabel>
              <OutlinedInput id="name" value={''} onChange={handleChange} label="Имя" />
              </FormControl>
            <FormControl variant="outlined" style={{left: "20px"}}>
              <InputLabel htmlFor="component-outlined">Фамилия</InputLabel>
              <OutlinedInput id="forename" value={''} onChange={handleChange} label="Фамилия" />
          </FormControl>
          </div>
          <div className = {style.secondRow}>
              <InputLabel htmlFor="component-outlined">Никнейи</InputLabel>
              <OutlinedInput id="nickname"  label="Фамилия" style={{width: "440px"}}/>
              <div>
              <FormControlLabel
              control={<GreenCheckbox checked={false} onChange={handleChange} name="checkedG" />}
                label="Не использовать никнейм"  id="noNickname"
            />
            </div>
          </div>
          <div className = {style.secondRow}>
              <InputLabel htmlFor="component-outlined">Электронная почта</InputLabel>
              <OutlinedInput id="email"  label="Фамилия" style={{width: "440px"}}/>
          </div>
          <div className = {style.thirdRow}>

          </div>
          <div className = {style.fourthRow}>
            <InputLabel htmlFor="component-outlined">Немного о себе</InputLabel>
            <TextField
              id="about"
              multiline
              rows={4}
              variant="outlined"
              style ={textBoxStyle}
            />
          </div>
            <Button variant="contained" style = {submitButtonStyle} onClick ={(e)=>{
              var Name = document.getElementById("name").value;
              var Forename = document.getElementById("forename").value;
              var Nickname = document.getElementById("nickname").value;
              var NoNickname = document.getElementById("noNickname").value;
              var Email = document.getElementById("email").value;
              var About = document.getElementById("about").value;
            }}>
              <a href = '#secondPage' style = {{textDecoration: "none"}}>Далее</a>
            </Button>
        </form>
      </div>
      <div className = {style.rightSide}>
        <h1 className = {style.noteIcon}>📝</h1>
        <h3 className = {style.noteLabel}>Немного формальностей</h3>
      </div>
    </div>
  );
}

export default FirstPage;