import React from 'react';
import style from './style.module.css';
import Greetings from '../Greetings/Greetings';
import QuickInfo from '../QuickInfo/QuickInfo';
import QuickNews from '../QuickNews/QuickNews';
import QuickCarousel from '../QuickCarousel/QuickCarousel';
import QuickInvite from '../QuickInvite/QuickInvite';
import QuickLocation from '../QuickLocation/QuickLocation';
import NavBar from '../NavBar/NavBar';

const Landing = () =>{
    return(
        <div className = {style.wrapper}>
            <NavBar/>
            <Greetings/>
            <QuickInfo/>
            <QuickNews/>
            <QuickCarousel/>
            <QuickInvite/>
            <QuickLocation/>
        </div>
    )
}

export default Landing;